@font-face {
  font-family: "swiper-icons";
  src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
  font-weight: 400;
  font-style: normal;
}
:root {
  --swiper-theme-color: #007aff;
}

.swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-pointer-events {
  touch-action: pan-y;
}
.swiper-pointer-events.swiper-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */
.swiper-autoheight,
.swiper-autoheight .swiper-slide {
  height: auto;
}
.swiper-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

/* 3D Effects */
.swiper-3d, .swiper-3d.swiper-css-mode .swiper-wrapper {
  perspective: 1200px;
}
.swiper-3d .swiper-wrapper,
.swiper-3d .swiper-slide,
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom,
.swiper-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}
.swiper-3d .swiper-slide-shadow,
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right,
.swiper-3d .swiper-slide-shadow-top,
.swiper-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}
.swiper-3d .swiper-slide-shadow {
  background: rgba(0, 0, 0, 0.15);
}
.swiper-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */
.swiper-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}
.swiper-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}
.swiper-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-horizontal.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-vertical.swiper-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

.swiper-centered > .swiper-wrapper::before {
  content: "";
  flex-shrink: 0;
  order: 9999;
}
.swiper-centered.swiper-horizontal > .swiper-wrapper > .swiper-slide:first-child {
  margin-inline-start: var(--swiper-centered-offset-before);
}
.swiper-centered.swiper-horizontal > .swiper-wrapper::before {
  height: 100%;
  width: var(--swiper-centered-offset-after);
}
.swiper-centered.swiper-vertical > .swiper-wrapper > .swiper-slide:first-child {
  margin-block-start: var(--swiper-centered-offset-before);
}
.swiper-centered.swiper-vertical > .swiper-wrapper::before {
  width: 100%;
  height: var(--swiper-centered-offset-after);
}
.swiper-centered > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: center center;
}

@font-face {
  font-family: "Roboto", "Arial", sans-serif;
  font-display: auto;
  src: url("../fonts/Roboto.woff2") format("woff2"), url("../fonts/Roboto.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Roboto Light", "Arial", sans-serif;
  font-display: auto;
  src: url("../fonts/Robotolight.woff2") format("woff2"), url("../fonts/Robotolight.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Roboto Medium", "Arial", sans-serif;
  font-display: auto;
  src: url("../fonts/Robotomedium.woff2") format("woff2"), url("../fonts/Robotomedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Roboto Bold", "Arial", sans-serif;
  font-display: auto;
  src: url("../fonts/Robotobold.woff2") format("woff2"), url("../fonts/Robotobold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}
html {
  height: 100%;
}

.overlay {
  overflow: hidden;
  height: 100vh;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}

h1 {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 55px;
  line-height: 55px;
}

h2,
h3 {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 30px;
}
@media (max-width: 1447px) {
  h2,
h3 {
    font-size: calc(
    18px + 12 * ((100vw - 320px) / 1127)
  );
  }
}

h4,
h5,
h6,
p {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
}

.container {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  padding: 0 140px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 75px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.icon {
  width: 100%;
  height: 2em;
}
.icon--sm {
  height: 5em;
}

.btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  padding: 1vw 3.75vw;
  font-family: "Roboto Medium", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  background: #fb565a;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: 0.5s;
  outline: none;
}
.btn:focus, .btn:hover {
  outline: none;
  background-color: #e74246;
  transition: 0.5s;
}
.btn:active {
  outline: none;
  background-color: #d7373b;
  box-shadow: inset 0 3px 0 rgba(0, 1, 1, 0.1);
  transition: 0.5s;
  opacity: 0.3;
}
.btn--green {
  background-color: #00ca74;
}
.btn--green:focus, .btn--green:hover {
  background-color: #00bc6c;
}
.btn--green:active {
  background-color: #00aa62;
}
.btn--yellow {
  background-color: #efc849;
}
.btn--yellow:focus, .btn--yellow:hover {
  background-color: #eab534;
}
.btn--yellow:active {
  background-color: #e5a722;
}

.page {
  position: relative;
  margin: 0;
  padding: 0;
  min-height: 100%;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 1em;
  font-weight: normal;
  line-height: normal;
  color: #000000;
  scroll-behavior: smooth;
}

@supports (display: flex) {
  .page {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
.page-header {
  width: 100%;
  padding-top: 48px;
  background-color: #eeeeee;
}
.page-header h1 {
  padding-top: 72px;
  padding-bottom: 108px;
  text-align: center;
}
.page-header .container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.page-header__nav {
  display: flex;
  justify-content: center;
  align-items: center;
}
.page-header__cart {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #000000;
  text-transform: uppercase;
}
.page-header__cart:focus, .page-header__cart:hover {
  color: #fb565a;
}
.page-header__cart:active {
  opacity: 0.3;
}
.page-header__cart svg {
  width: 15px;
  height: 15px;
}
.page-header__cart span {
  padding-left: 25px;
}

.logo {
  display: flex;
}
.logo:focus, .logo:hover {
  opacity: 0.7;
}
.logo:active {
  opacity: 0.3;
}
.logo img {
  width: 160px;
  height: 65px;
}

.nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.nav__link {
  position: relative;
  display: inline-block;
  padding: 10px 0;
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #000000;
  text-transform: uppercase;
}
.nav__link:not(:last-child) {
  margin-right: 45px;
}
.nav__link:focus, .nav__link:hover {
  color: #fb565a;
}
.nav__link:active {
  opacity: 0.3;
}
.nav__link--active::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: #fb565a;
}

.page-footer .container {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  padding-top: 68px;
  padding-bottom: 68px;
}
.page-footer__text {
  padding-left: 140px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #444444;
}
.page-footer__text--big {
  padding-bottom: 10px;
  font-weight: bold;
  font-size: 36px;
  line-height: 36px;
  color: #000000;
}

.promo {
  position: relative;
  width: 100%;
  max-width: 100%;
  height: 100%;
  padding: 0;
  margin-bottom: 40px;
  background-color: #eeeeee;
  color: #283136;
}
.promo__info {
  z-index: 2;
  position: relative;
  max-width: 400px;
}
.promo__text-big {
  font-weight: 500;
  font-size: 55px;
  line-height: 55px;
  margin-bottom: 25px;
  color: #000000;
}
.promo .btn {
  margin-top: 38px;
}
.promo .slider .swiper-slide {
  padding: 20px 0 50px;
  opacity: 0;
  background-repeat: no-repeat;
  background-position: 100% 0;
  background-size: contain;
}
.promo .slider .swiper-slide-active {
  opacity: 1;
}

.categories {
  padding-top: 40px;
  padding-bottom: 40px;
}
.categories .container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 6vw;
  transition: 0.5s;
}
.categories__item {
  display: flex;
  flex-direction: column;
  padding-bottom: 40px;
  max-width: 300px;
  width: 100%;
  color: #283136;
}
.categories__pic {
  padding-bottom: 25px;
}
.categories__title {
  font-family: "Roboto Bold", "Arial", sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 30px;
  padding-bottom: 16px;
  color: #000000;
  text-transform: uppercase;
}
.categories__text {
  padding-bottom: 32px;
}
.categories__btn {
  align-self: flex-start;
  margin-top: auto;
}

.about-company {
  position: relative;
  padding-top: 40px;
  padding-bottom: 40px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.about-company::before {
  content: "";
  position: absolute;
  top: 0;
  left: 9.72vw;
  width: calc(100% - 19.44vw);
  height: 2px;
  background-color: #eeeeee;
}
.about-company__info {
  max-width: 655px;
}
.about-company__info h3 {
  padding-top: 40px;
  padding-bottom: 23px;
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
}
.about-company__text-big {
  padding-bottom: 32px;
  font-weight: 500;
  font-size: 45px;
  line-height: 45px;
}
.about-company__list {
  padding: 0;
  margin: 0;
  list-style: none;
  color: #283136;
}
.about-company__list li {
  position: relative;
  padding-left: 30px;
  margin-bottom: 30px;
}
.about-company__list li::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 25px;
  height: 2px;
  background-color: #fb565a;
}

.advantages {
  background-image: url(../img/info/nerds-illustration1.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50% 0;
  padding-top: 245px;
  font-weight: bold;
  text-transform: uppercase;
  color: #000000;
}
.advantages__list {
  padding: 30px 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.advantages__item {
  display: flex;
  flex-direction: column;
  max-width: 120px;
  font-weight: normal;
  color: #283136;
  text-transform: lowercase;
}
.advantages__item span:first-child {
  position: relative;
  align-self: flex-start;
  font-weight: bold;
  font-size: 45px;
  line-height: 64px;
  color: #000000;
}
.advantages__item span:first-child::before {
  content: "%";
  position: absolute;
  top: 6px;
  right: -20px;
  font-weight: bold;
  font-size: 26px;
  line-height: 40px;
}

.clients {
  position: relative;
  padding: 46px 0 44px;
  margin-bottom: 80px;
}
.clients::before {
  content: "";
  position: absolute;
  top: 0;
  left: 9.72vw;
  width: calc(100% - 19.44vw);
  height: 2px;
  background-color: #eeeeee;
}
.clients::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 9.72vw;
  width: calc(100% - 19.44vw);
  height: 2px;
  background-color: #eeeeee;
}
.clients__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.clients__item img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.map {
  min-height: 415px;
  background-image: url(../img/map/map1.jpg);
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover;
}
.map__info {
  padding: 54px 0;
  margin-left: 0;
  margin-right: auto;
}

.contacts {
  padding: 50px;
  display: flex;
  flex-direction: column;
  max-width: 320px;
  width: 100%;
  background-color: #ffffff;
}
.contacts__title {
  font-weight: bold;
  font-size: 18px;
  line-height: 30px;
  padding-bottom: 22px;
}
.contacts__address {
  color: #666666;
  padding-bottom: 10px;
}
.contacts__phone {
  color: #666666;
  text-decoration: none;
  margin-bottom: 37px;
}
.contacts .btn {
  padding-left: 3.3vw;
  padding-right: 3.3vw;
}

.social__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}
.social__item {
  width: 81px;
  height: 81px;
}
.social__item:not(:last-child) {
  margin-right: 9px;
}
.social__item:first-child .social__link {
  padding-left: 26px;
}
.social__link {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px;
  background: linear-gradient(0deg, #e1e1e1, #e1e1e1), #ffffff;
  border-radius: 50%;
}
.social__link:focus .icon, .social__link:hover .icon {
  opacity: 0.3;
}
.social__link .icon {
  height: 22px;
}

.popup {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  box-sizing: border-box;
}
.popup--active {
  display: flex;
}
.popup__wrapper {
  position: relative;
  padding: 63px 100px 84px;
  margin: auto;
  max-width: 960px;
  width: 100%;
  background: #ffffff;
  box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.4);
}
.popup__close {
  position: absolute;
  top: 78px;
  right: 90px;
  width: 28px;
  height: 28px;
  padding: 0;
  background-color: transparent;
  border: none;
  border-radius: 0;
  cursor: pointer;
}

.feedback-form__wrapper {
  border: none;
  border-radius: 0;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.feedback-form__wrapper label {
  width: calc(50% - 20px);
}
.feedback-form legend {
  padding-bottom: 37px;
  font-style: normal;
  font-weight: bold;
  font-size: 45px;
  line-height: 53px;
}
.feedback-form label {
  display: flex;
  flex-direction: column;
  padding-bottom: 34px;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 18px;
}
.feedback-form input, .feedback-form textarea {
  margin-top: 9px;
  padding: 16px;
  background: #ffffff;
  border: 2px solid #d7dcde;
  box-sizing: border-box;
  border-radius: 3px;
}
.feedback-form input::placeholder, .feedback-form textarea::placeholder {
  color: #444444;
  opacity: 0.5;
}
.feedback-form textarea {
  resize: none;
}

.catalog {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 55px;
  padding-bottom: 60px;
}
.catalog__filter {
  max-width: 260px;
  width: 100%;
}
.catalog__wrapper {
  max-width: calc(100% - 260px - 140px);
  width: 100%;
}
.catalog__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}
.product {
  width: 100%;
}
.product__header {
  padding: 10px;
  background-color: #4d4d4d;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.product__header svg {
  fill: #ffffff;
}