.nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  &__link {
    position: relative;
    display: inline-block;
    padding: 10px 0;
    text-decoration: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: $primary-color;
    text-transform: uppercase;

    &:not(:last-child) {
      margin-right: 45px;
    }

    &:focus,
    &:hover {
      color: $accent-color;
    }

    &:active {
      opacity: $opacity-base;
    }

    &--active {
      &::before {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        background-color: $accent-color;
      }
    }
  }
}
